*{
  margin:0;
  padding: 0;
}


body{
  background-color: rgb(176, 207, 213);
}.app{

text-align: center;
color:blue;
}h1{
  color:green;

}.app input{
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid grey;
  outline:none;
  padding: 10px 5px;
  width:200px;

}.app-input button{
  background-color: rgb(3, 14, 0);
  color:white;  
  border:none;
  outline:none;
  margin-top:10px;
  font-size: 16px;
  cursor: pointer;
} .navActive{
  color:chartreuse;
  font-size:30px;
  font-weight: bold;

}nav{
  background-color: grey;
  padding: 10px 0;
  display: flex;
  justify-content: space-between;
  margin-top: 5px;
}nav ul{
  display: flex;
  list-style-type:none;
  justify-content: space-between;
  align-items: center;
  width: 40%;
    color: rgb(20, 20, 20);
    font-weight: bold;
}nav ul li{
  cursor: pointer;
  font-size: 14px;
  transition: all 0.5s ease-in-out;
}nav ul li :hover{
transform: scale(1.2);
}